@import "theme1";

//Style Four
#atw_style4{
    @extend #atw_style1;
    .atw__title4{
        margin-bottom: 50px;
        padding-bottom: 0;
        h3{
            position: relative;
            &:before{
                position: absolute;
                content: "";
                width: 40%;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
            &:after{
                position: absolute;
                content: "";
                width: 40%;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
        }
        &::before{
            content: none;
        }
    }
    .atw--single_item{
        .atw_item_top{
            .atw_overlay_content{
                background: none;
                width: 100%;
                padding: 0 10px;
                bottom: 0;
                left: auto;
                .atw_product_actions{
                    box-shadow: none;
                    display: block;
                    li{
                        display: block;
                        margin-bottom: 5px;
                        text-align: right;
                        &:last-child{
                            margin-bottom: 0;
                        }
                        a{
                            display: inline-block;
                            height: 45px;
                            width: 45px;
                            border: 1px solid #e4e4ed;
                            background: #fff;
                            padding-top: 12px;
                            text-align: center;
                        }
                    }
                }
            }
        }
        .atw_item_bottom{
            position: relative;
            .atw_item_cat{
                font-size: 12px;
                color: #9192a3;
                display: block;
                margin-bottom: 10px;
            }
            .atw_item_info{
                .atw_price{
                    transition: 0.3s ease;
                }
            }
            .atw_btn{
                visibility: hidden;
                opacity: 0;
                transition: 0.3s ease;
                position: absolute;
                left: 0;
                bottom: 0;
            }
        }
        &:hover{
            .atw_item_bottom{
                .atw_item_info{
                    .atw_price{
                        visibility: hidden;
                        opacity: 0;
                    }
                }
                .atw_btn{
                    visibility: visible;
                    opacity: 1;
                    bottom: 5px;
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control4{
            &.icon-arrow-left{
                border-radius: 3px;
            }
            &.icon-arrow-right{
                border-radius: 3px;
            }
            &.icon-arrow-left, &.icon-arrow-right{
                transition: 0.3s ease;
                &:hover{
                    color: $theme1;
                    border-color: $theme1;
                }
            }
        }
    }
}